// main page elements
body {
	font-size: 14px;
	color: $dark;
	font-family: 'Gilroy', sans-serif;
	font-weight: 400;
	background: white;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

* {
	font-family: 'Gilroy', sans-serif; }


.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	max-width: 1170px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }

.no-page {
	margin: 150px 0;
	.action {
		max-width: 350px; } }

.block-title {
	font-family: 'Mandy', serif;
	font-size: 35px;
	color: $pink;
	margin: 0 0 36px 0;
	@include mq($max-width: 1200px) {
		margin: 0 0 20px 0; }
	@include mq($max-width: 575px) {
		font-size: 24px; } }

.arrow {
	position: absolute;
	top: 50%;
	z-index: 3;
	cursor: pointer;
	background: transparent;
	@include mq($max-width: 1200px) {
		top: -45px; }
	@include mq($max-width: 700px) {
		top: -35px; }
	&:hover {
		svg {
			path {
				fill: $green; } } }
	svg {
		display: block;
		width: 50px;
		height: auto;
		@include mq($max-width: 1200px) {
			width: 30px; }
		path {
			@include trs;
			fill: $pink; } }
	&_prev {
		transform: translate(-120%, -50%);
		left: 0;
		@include mq($max-width: 1200px) {
			right: 15px;
			left: auto; } }
	&_next {
		transform: translate(120%, -50%);
		right: 0;
		@include mq($max-width: 1200px) {
			right: 0;
			transform: translate(0, -50%); } } }






.slick-track {
	display: flex;
	align-self: stretch;
	height: 100%; }

