.feedback {
    // temp
    margin: 0 auto;
    max-width: 540px;
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    padding: 40px;
    @include mq($max-width: 575px) {
        padding: 15px; }

    // temp

    &__subtitle {
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        text-align: center;
        color: $form;
        margin-bottom: 40px; }

    &__questions {
        font-style: normal;
        font-weight: normal;
        font-size: 21px;
        line-height: 25px;
        text-align: center;
        color: $form;
        padding: 20px 0; }

    &__form {
        max-width: 388px;
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center; }

    &__textarea {

        height: 140px;
        width: 100%;
        border: 1px solid $form_border;
        font-style: normal;
        font-weight: normal;
        font-size: 17px;
        padding: 24px;
        background-color: $form_bg;
        &::placeholder {
            color: $line_2; } }

    &__input {
        width: 100%;
        height: 65px;

        border: 1px solid $form_border;
        margin-bottom: 20px;
        font-style: normal;
        font-weight: normal;
        font-size: 17px;
        padding: 0 20px 0 60px;
        background-color: $form_bg;
        &::placeholder {
            color: $line_2; } }

    &__label {
        position: relative;
        display: flex;
        width: 100%;
        svg {
            position: absolute;
            height: 22px;
            width: 22px;
            top: 20px;
            left: 24px; } }

    &__consent {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        max-width: 310px;
        padding: 20px 0 0 0; } }
